import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api'

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  public corpus: {word: string, highlight: boolean}[][][] = [];

  public alignments: string[][] = [];
  public corpusName: string = 'agr-ch';
  constructor(public navCtrl: NavController,
    public api: ApiProvider) {

    // this.initCorpus(c);
  }

  async ionViewDidLoad() {
    await this.loadCorpus();
  }

  public async loadCorpus(): Promise<void> {
    this.corpus = [];
    this.alignments = [];
    let cName = this.corpusName.slice(0, 6);
    let corpus = await this.api.get(`${cName}.json`);
    this.initCorpus(corpus);
    this.alignments = await this.api.get(`${this.corpusName}-align.json`);
  }

  public wordClicked(wordIndex, index, line): void {
    console.log(wordIndex, index, line)
    this.corpus[line][index][wordIndex].highlight = !this.corpus[line][index][wordIndex].highlight;
    this.findCoRelated(this.corpus[line][index][wordIndex].word, index, line, this.corpus[line][index][wordIndex].highlight);
  }

  public findCoRelated(word, index, line, flag): void {
    let otherIndex = index == 0 ? 1 : 0;
    for (let i = 0; i < this.alignments.length; i++) {
      if (this.alignments[i][index] == word) {
        for (let j = 0; j < this.corpus[line][otherIndex].length; j++) {
          if (this.corpus[line][otherIndex][j].word == this.alignments[i][otherIndex]) {
            let p = parseFloat(this.alignments[i][2])
            if (p > 0.15) {
              this.corpus[line][otherIndex][j].highlight = flag;
            }
          }
        }
      }
    }
  }

  public initCorpus(corpus): void {
    for (let i = 0; i < corpus.length; i++) {
      let srcLine = [];
      let targetLine = []
      for (let j = 0; j < corpus[i][0].length; j++){

        srcLine.push({
          word: corpus[i][0][j],
          highlight: false
        });
      }

      for (let j = 0; j < corpus[i][1].length; j++){
        targetLine.push({
          word: corpus[i][1][j],
          highlight: false
        });
      }
      this.corpus.push([srcLine, targetLine]);
    }
  }

}



